#header {
  position: absolute;
  left: 0;
  top: 40px;
  width: 100%;
  z-index: 10; }

.header-content {
  display: flex;
  align-items: center; }
  @media (max-width: 767px) {
    .header-content {
      align-items: flex-start;
      justify-content: space-between; } }

.logo {
  width: 60px;
  height: 92px; }

.header__logo {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between; }

.logo__wrap {
  display: flex;
  width: 60px;
  height: 73px;
  position: relative; }
  .logo__wrap .logo__item {
    position: absolute; }
    .logo__wrap .logo__item:nth-child(1) {
      top: 0;
      left: 0;
      animation-name: item1;
      animation-duration: 1.5s; }

@keyframes item1 {
  0% {
    left: 35px; }
  100% {
    left: 0; } }
    .logo__wrap .logo__item:nth-child(3) {
      right: 0;
      bottom: 0;
      animation-name: item2;
      animation-duration: 1.5s; }

@keyframes item2 {
  0% {
    right: 35px; }
  100% {
    right: 0; } }

.logo__title {
  height: 12px;
  overflow: hidden;
  animation-name: title-logo;
  animation-duration: 1.5s; }

@keyframes title-logo {
  0% {
    height: 0; }
  100% {
    height: 12px; } }
  .logo__title p {
    font-size: 12.5px;
    line-height: 15px;
    color: #55B4ED; }

/* ____________________________ */
.menu__burger {
  display: none; }

.menu__body .menu__link {
  color: #414141; }

.menu__list li {
  position: relative;
  list-style: none; }
  .menu__list li .menu__arrow {
    position: absolute;
    right: -15px;
    top: 50%;
    transition: all 0.5s ease 0s; }

.menu__list .menu__link {
  position: relative; }

/* ____________________________ */
._pc .menu__list li:hover .menu__arrow {
  transform: rotate(180deg); }

._pc .menu__list li:hover .menu__sub-list {
  opacity: 1;
  visibility: visible;
  pointer-events: all; }

._touch .menu__list ._active .menu__arrow {
  transform: rotate(180deg); }

._touch .menu__list ._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  pointer-events: all; }

/* ____________________________ */
@media (min-width: 769px) {
  .menu__list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    font-size: 20px; }
    .menu__list .mob-link {
      display: none; }
    .menu__list li {
      margin-left: 40px; }
      .menu__list li:last-child {
        margin-left: 60px; }
      .menu__list li .menu__link {
        position: relative;
        cursor: pointer;
        line-height: 1;
        text-decoration: none; }
        .menu__list li .menu__link:after {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          width: 0;
          height: 2px;
          background-color: #55B4ED;
          transition: width 0.3s ease-out; }
        .menu__list li .menu__link:hover:after, .menu__list li .menu__link:focus:after {
          width: 100%; }
  .menu__sub-list {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease 0s; } }

@media (max-width: 767px) {
  .menu__burger {
    z-index: 5;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer; }
    .menu__burger::before, .menu__burger::after, .menu__burger span {
      left: 0;
      position: absolute;
      height: 10%;
      width: 100%;
      transition: all 0.3s ease 0s;
      background-color: #55B4ED; }
    .menu__burger::before, .menu__burger::after {
      content: ''; }
    .menu__burger::before {
      top: 0; }
    .menu__burger::after {
      bottom: 0; }
    .menu__burger span {
      top: 50%;
      transform: scale(1) translate(0px -50%); }
  .menu__body {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../images/bg-img/mob-menu-bg.svg) center/cover no-repeat;
    background-color: #000;
    transition: all 0.3s ease 0s; }
    .menu__body .menu__link {
      font-weight: bold;
      font-size: 36px;
      color: #fff; }
  .menu__list {
    margin: 20vh 0 0 0;
    text-align: center; }
    .menu__list li {
      margin-bottom: 2vh; }
  .menu__sub-list {
    position: relative; } }

.menu__sub-list {
  position: absolute;
  padding: 0;
  top: 100%;
  min-width: 135px;
  font-size: 12px;
  line-height: 14px;
  padding-top: 9px; }
  .menu__sub-list li {
    margin: 14px 0 0 0; }
    .menu__sub-list li:nth-child(n+2) {
      position: relative; }
      .menu__sub-list li:nth-child(n+2)::before {
        content: '';
        position: absolute;
        top: -7px;
        left: 3px;
        height: 1px;
        width: 75px;
        background: #919191; }
    .menu__sub-list li:first-child {
      margin: 0 0 0 0; }
    .menu__sub-list li:last-child {
      margin-left: 0; }
    .menu__sub-list li a {
      color: #919191; }

